<template>
  <h3 class="my-4 text-2xl">
    Shaxsiy ma'lumotlar
  </h3>
  <JuvenileInitialPersonalInfoForm :clear-form-mode="clearForm" @submit="onSubmit" />
</template>

<script setup>
import JuvenileInitialPersonalInfoForm from "./JuvenileInitialPersonalInfoForm";
import JuvenilesService from "../../../services/juveniles.service";
import useClearForm from "../../../composables/useClearForm";
import useAlert from "../../../composables/useAlert";

const {alertIt} = useAlert()
const {clearForm,setClearForm} = useClearForm()
const {createJuvenilePersonalInfo} = JuvenilesService

const onSubmit = async (formData)=>{
  const {data:{message}} = await createJuvenilePersonalInfo(formData)
  if(message){
    alertIt(message,'success')
    setClearForm(true)
  }
}
</script>

