<template>
  <form @submit="onSubmit">
    <section class="section_wrapper">
      <div class="grid grid-cols-2 xl:grid-cols-3 gap-4 2xl:w-[60%]">
        <div class="form_wrapper my-3">
          <label class="input_label" for="last_name">
            Familiya
          </label>
          <input v-validate-cyrillic id="last_name" v-model="last_name" class="base_input" placeholder="Familiyasini kiriting" name="last_name" type="text">
          <span class="error_message">{{ last_nameError }}</span>
        </div>
        <div class="form_wrapper my-3">
          <label for="first_name" class="input_label">
            Ismi
          </label>
          <input v-validate-cyrillic id="first_name" v-model="first_name" placeholder="Ismini kiriting" class="base_input" name="last_name" type="text">
          <span class="error_message">{{ first_nameError }}</span>
        </div>
        <div class="form_wrapper my-3">
          <label class="input_label" for="father_name">
            Sharifi
          </label>
          <input v-validate-cyrillic id="father_name" v-model="father_name" placeholder="Sharifini kiriting" class="base_input" name="father_name" type="text">
          <span class="error_message">{{ father_nameError }}</span>
        </div>
        <div class="form_wrapper my-3">
          <label class="input_label">
            Jinsi
          </label>
          <ButtonGroup id="gender" v-model:selected-item="gender" :titles="btnGroupGenders" />
          <span class="error_message">{{ genderError }}</span>
        </div>
        <div class="form_wrapper my-3">
          <label class="input_label">
            Tugâ€˜ilgan sanasi
          </label>
          <DateComponent  
              v-model="birth_date" 
              :options="{
                startDate:birthDateRange.from,
                minDate:birthDateRange.from,
                maxDate:birthDateRange.to,
              }"  
          />
          <span class="error_message">{{ birth_dateError }}</span>
        </div>
        <div class="form_wrapper my-3">
          <label class="input_label" for="birth_region">
            Tug'ilgan Viloyat / Shahar
          </label>
          <SelectBox
            id="birth_region" v-model="birth_region" :options="regionsList" show-by="name" track-by="id" placeholder="Viloyatni tanlang"
            @input="birthPlaceFetchDistrictsByRegionId"
          />
          <span class="error_message">{{ birth_regionError }}</span>
        </div>
        <div class="form_wrapper my-3">
          <label class="input_label" for="birth_district">
            Tug'ilgan Tumani
          </label>
          <SelectBox id="birth_district" v-model="birth_district" :options="birthPlaceDistrictList" show-by="name" track-by="id" placeholder="Tumanni tanlang" />
          <span class="error_message">{{ birth_districtError }}</span>
        </div>
      </div>
      <div class="2xl:w-[60%]">
        <div class="form_wrapper my-3">
          <label id="photo" class="input_label">
            Rasmi
          </label>
          <FileInput :id="'photo1'" v-model:files="photo" :multiple-files="false" :initial-files="initialUserPhoto">
            <template #icon>
              <div class="flex justify-between shadow rounded w-full">
                <p class="flex items-center text-gray-400  px-3 overflow-hidden overflow-ellipsis base_input border-none">
                  Rasmni yuklang
                </p>
                <span class="flex px-4 items-center justify-center rounded bg-primary text-white tracking-wide text-sm cursor-pointer">
                  YUKLASH
                </span>
              </div>
            </template>
          </FileInput>
          <span class="error_message">{{ photoError }}</span>
        </div>
      </div>
    </section>
    <button class="px-3 m-2 py-2 border bg-primary text-white rounded-md" type="submit" :disabled="isSubmitting">
      Saqlash
    </button>
  </form>
</template>

<script setup>
import {useField, useForm} from 'vee-validate';
import * as yup from 'yup';
import {defineEmits, defineProps, onMounted, reactive, ref, toRefs, watch} from 'vue';
import {requiredValidatorMessage} from "../../../utils/validatorMessages";
import SelectBox from "../../Common/SelectBox";
import FileInput from "../../Common/InputFile/FileInput";
import useRegion from "../../../composables/selectbox-list/useRegion";
import useDistrict from "../../../composables/selectbox-list/useDistrict";
import {btnGroupGenders} from '../../../utils/buttonGroupTitles'
import dayjs from "dayjs";
import useScrollToError from "../../../composables/useScrollToError";
import ButtonGroup from "../../Common/ButtonGroup";
import DateComponent from "../../Common/DateComponent";

const {handleSubmit, isSubmitting,resetForm} = useForm();
const {regionsList} = useRegion()
const {districtsList: birthPlaceDistrictList, fetchDistrictsByRegionId: birthPlaceFetchDistrictsByRegionId} = useDistrict()
const {scrollToError} = useScrollToError()
const emit = defineEmits(['submit'])
const props = defineProps({
  initialFields:{
    type:Object,
    default:function (){
      return null
    }
  },
  clearFormMode:{
    type:Boolean,
    default:false
  },
  isEditMode:{
    type:Boolean,
    default:false
  }
})
const {initialFields,clearFormMode} = toRefs(props)


function initializeFields(fieldsFromServer){
  gender.value = fieldsFromServer.gender
  father_name.value = fieldsFromServer.father_name
  first_name.value = fieldsFromServer.first_name
  last_name.value = fieldsFromServer.last_name
  if(fieldsFromServer.photo){
    initialUserPhoto.value = [fieldsFromServer.photo]
  }
  if(fieldsFromServer.birth_region){
    birth_region.value = fieldsFromServer.birth_region.id
    birth_district.value = fieldsFromServer.birth_district.id
    birthPlaceFetchDistrictsByRegionId({target:{value:fieldsFromServer.birth_region.id}})
  }
}

onMounted(()=>{
  if(initialFields.value){
    initializeFields(initialFields.value)
  }
})

const birthDateRange = reactive({
  from :(dayjs().subtract(18,'year')).toDate(),
  to:(dayjs().subtract(3,'year')).toDate()
})

const initialUserPhoto = ref([])

function onInvalidSubmit({ errors}) {
  console.log(errors)
  scrollToError(Object.keys(errors)[0])
}
const onSubmit = handleSubmit(async ({birth_date, ...args}) => {
  try {
    const payloadObj = {
      is_identified:false,
      birth_date: dayjs(birth_date).format('YYYY-MM-DD'),
      ...args
    }
    const formData = new FormData();
    Object.keys(payloadObj).forEach(k => {
      formData.append(k, payloadObj[k])
    })
    emit('submit',formData)
  } catch (e) {
    console.log(e)
  }
}, onInvalidSubmit);





const {
  value: last_name,
  errorMessage: last_nameError,
} = useField('last_name', yup.string().required(requiredValidatorMessage) ,{initialValue: ''});

const {
  value: first_name,
  errorMessage: first_nameError
} = useField('first_name', yup.string().required(requiredValidatorMessage), {initialValue: ''});

const {
  value: father_name,
  errorMessage: father_nameError
} = useField('father_name',yup.string().required(requiredValidatorMessage), {initialValue: ''});

const {
  value: birth_date,
  errorMessage: birth_dateError
} = useField('birth_date', yup.date().nullable().required(requiredValidatorMessage));

const {
  value: birth_region,
  errorMessage: birth_regionError
} = useField('birth_region', yup.string().required(requiredValidatorMessage), {
  initialValue: ""
});

const {
  value: birth_district,
  errorMessage: birth_districtError
} = useField('birth_district', yup.string().required(requiredValidatorMessage), {
  initialValue: ""
});



const {value: gender,
  errorMessage: genderError
} = useField('gender', yup.string().required(requiredValidatorMessage));


const {
  value: photo,
  errorMessage: photoError
} = useField('photo',yup.mixed().nullable().required(requiredValidatorMessage),{
  initialValue: null
});

watch(()=>clearFormMode.value,(clearFormMode)=>{
  if(clearFormMode === true){
    resetForm()
  }
})

</script>

