import {ref} from 'vue';
import RegionsService from '../../services/regions-districts.service';

export default function useDistrict() {
  const districtsList = ref([]);
  const fetchDistrictsByRegionId = async (val) => {
    const {data} = await RegionsService.fetchDistrictsByRegionId(val);
    districtsList.value = data;
  };

  return {
    districtsList,
    fetchDistrictsByRegionId,
  };
}
